@import url(https://fonts.googleapis.com/css2?family=Caveat+Brush&family=Open+Sans&display=swap);
/* Variables */

:root {
  --ft-family-text: "Open Sans", sans serif;
  --ft-family-heading: 'Caveat Brush', cursive;
  --color-lilac: hsl(281.71, 75.93%, 78.82%);
  --color-pink: hsl(292.6, 65.81%, 54.12%);
  --color-light-pink: hsl(291.86, 67.05%, 65.49%);
  --color-deep-purple: hsl(257.14, 62.5%, 21.96%);
  --color-light-lilac: hsl(252.35, 70.83%, 90.59%);
  --color-light-black: hsl(0, 0%, 13.33%);
  --color-black: hsl(0, 0%, 0%);
  --color-white: hsl(0, 0%, 100%);
}


*, 
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  width: 100%;
  height: 100%;
  margin: 0;
  font-size: 62.5%;
  
}

body {
  height: 100%;
  margin: 10px 0;
  padding: 0;
  box-sizing: inherit;
  font-family: "Open Sans", sans serif;
  font-family: var(--ft-family-text);
  background: linear-gradient(hsl(292.6, 65.81%, 54.12%), hsl(257.14, 62.5%, 21.96%));
  background: linear-gradient(var(--color-pink), var(--color-deep-purple));
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, 
h2 { 
  font-family: 'Caveat Brush', cursive; 
  font-family: var(--ft-family-heading);
  font-size:5rem;

}


.appWrap {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 10px;
  min-width: 1100px;
}

.editorWrap {
  margin-left: 10%;
  min-width: 400px;
  align-self: flex-start;
}

 .toolbar {
  background: var(--color-light-lilac);
  width: 80%;
  font-family: var(--ft-family-text);
  font-size: 1.2em;
  font-weight: 700;
  padding: 7px 15px;
  border-style: solid;
  border-width: 1.5px;
  border-bottom-width: 0px;
  border-radius: 10px 10px 0px 0px;
  box-shadow: 15px 8px 10px 5px rgba(0, 0, 0, 0.3);
}

.fa-free-code-camp {
  margin-right: 3px;
}

i {
  width: 25px;
  color: black;
  margin-left: 5px;
}

textarea {
  background: var(--color-lilac); 
  width: 99%;
  min-height: 200px;
  margin-bottom: -5px;
  resize: vertical;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 12px;
}


#editor {
  height: 60vh;
  width: 80%;
  min-height: 500px;
  min-width: 300px;
  padding: 10px 15px;
  border-style: solid;
  border-width: 1.5px;
  border-bottom-width: 0px;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 15px 8px 10px 5px rgba(0, 0, 0, 0.3);
}

.previewWrap {
  min-width: 600px;
}

#preview {
   width: 80%;
   height: 75vh;
   min-height: 550px;
   background: var(--color-lilac);
   overflow: auto;
   padding: 0px 15px;
   border-style: solid;
   border-width: 1.5px;
   border-radius: 0px 0px 10px 10px;
   box-shadow: 15px 15px 10px 5px rgba(0, 0, 0, 0.3);
}

.converter {
  width: 100px;
  text-align: center;
  font-size: 35px;
  margin: auto;
}

/* Markdown Styles */
  blockquote {
    border-left: 3px solid #224B4B;
    color: #224B4B;
    padding-left: 5px;
    margin: 15px 0;
  }

  code {
    background: white;
    padding: 1px 4px 2px 4px;
    font-size: 12px;
    font-weight: bold;
  }

  ol,
  ul  {
    margin: 10px;
  }

  h1 {
    border-bottom: 2px solid var(--color-deep-purple);
    margin-bottom: 10px;
  }

  h2 {
    border-bottom: 1px solid var(--color-deep-purple);
    margin-bottom: 10px;
    font-size: 28px;
  }

  h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }

  table {
    border-collapse: collapse;
    margin-top: 10px;
    background: white;
  }

  td, 
  th {
    border: 2px solid var(--color-deep-purple);
    padding-left: 5px;
    padding-right: 5px;
  }

/* Media Queries*/
@media screen and (max-width: 850px) {
  .appWrap {
    margin-top: 10px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 100px;
  }
  .editorWrap {
    min-width: 100px;
    margin-left: 17.5vw;
    margin-top: 0px;
  }
  .editorWrap .toolbar {
    width: 60vw;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
  }
  .editorWrap #editor {
    width: 60vw;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
    min-width: 100px;
    min-height: 200px;
    height: 25vh;
  }
  .previewWrap {
    margin-top: 30px;
    min-width: 100px;
  }
  .previewWrap .toolbar {
    width: 80vw;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
  }
  .previewWrap #preview {
    width: 80vw;
    height: 35vh;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
    min-height: 300px;
  }
}

.jumbotron {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    border-radius: 0 !important;
    /*box-shadow: 10px 10px 15px var(--color-black);*/
}

@media screen and (max-width: 850px) {
    .jumbotron {
        text-align: center;
    }
}


.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--ft-family-text);
    font-size: 1.4rem;
    color: var(--color-pink);
    height: 120px;
    margin: 40px auto;
}

footer p {
    width: 80%;
    text-align: center;
    justify-content: center;
}
span {
    color: white;
}

span:active
span:hover {
    color: var(--color-pink);
}

span:visited {
    color: gray;
}

@media screen and (max-width: 850px){
    footer {
        width: 60vw;
    }
}
  
